import React, { useEffect, useState } from "react";
import BlogCard from "../BlogCard/BlogCard";

const Blog = () => {
  const [blogCardData, setBlogCardData] = useState([]);

  useEffect(() => {
    fetch(
      "https://api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fmedium.com%2Ffeed%2F%40wickedaf"
    )
      .then((res) => res.json())
      .then((data) => setBlogCardData(data.items));
  }, []);

  return (
    <div className="container my-5 py-5">
      <div id="blog">
        <p className="display-4 py-3">.blog()</p>
      </div>
      <div className="row d-flex justify-content-center">
        {blogCardData.map((cardData, index) => (
          <BlogCard key={index} cardData={cardData}></BlogCard>
        ))}
      </div>
    </div>
  );
};

export default Blog;
