import React from "react";
import emailjs from "emailjs-com";

const Contact = () => {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm("service_s99ic3e", "template_t7fgib5", e.target, "user_8AwFmWHg2hU2L8AFAMV7M")
      .then(
        (result) => {
          console.log(result.text);
          alert("Your message has been sent! ðŸ™‚");
        },
        (error) => {
          console.log(error.text);
        }
    );
    e.target.reset();
  }

  return (
    <div id="contact" className="container my-5 py-5">
        <p className="display-4 py-3">.contact()</p>
      <div className="d-flex justify-content-center">
        <div className="col-md-6">
          <form className="contact-form" onSubmit={sendEmail}>
            <input placeholder="Your Name" className="form-control border-0 rounded shadow my-2" type="text" name="from_name"style={{backgroundColor: "var(--light-navy)"}} />
            <input placeholder="Your Email" className="form-control border-0 rounded shadow my-2" type="email" name="email" style={{backgroundColor: "var(--light-navy)"}}/>
            <textarea placeholder="Write your message" className="form-control border-0 rounded shadow my-2" name="message" style={{backgroundColor: "var(--light-navy)"}}/>
            <input className="form-control rounded shadow btn btn-dark" type="submit" value="Send" />
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
