import React from "react";
import PortfolioCard from "../PortfolioCard/PortfolioCard";

const Portfolio = () => {
  const portfoilioCardData = [
    {
      id: 1,
      name: "Go Clean",
      description:
        "A service oriented site, where user can order any service and can checkout using Stripe Payment Gateway",
      image: "https://media.giphy.com/media/L5qxYojmCG6MidQw3a/giphy.gif",
      technology: ["React", "NodeJs", "ExpressJs", "MongoDB"],
      liveLink: "https://go-clean-react.web.app/",
      gitLink: "https://github.com/wickedaf/goclean-frontend",
    },
    {
      id: 2,
      name: "Bazaar Shodai",
      description:
        "An e-commerce site with Product Management. A sleek looking dashboard for Admin. User can sign in using Social Sign such as Facebook, Google.",
      image: "https://media.giphy.com/media/CkQLkNIhVFL8RQzyYv/giphy.gif",
      technology: ["React", "NodeJs", "ExpressJs", "MongoDB"],
      liveLink: "https://bazaar-shodai.web.app/",
      gitLink: "https://github.com/wickedaf/bazaar-shodai-frontend",
    },
    {
      id: 3,
      name: "Tripstation",
      description:
        "A ride sharing web application with google map integrated. User can order a ride by singing using Google or Facebook",
      image: "https://media.giphy.com/media/zxtC6S1nLUPVNGW36y/giphy.gif",
      technology: ["React", "NodeJs", "ExpressJs", "MongoDB"],
      liveLink: "https://tripstation-react.web.app/",
      gitLink: "https://github.com/wickedaf/tripstation-react",
    },
  ];


  return (
    <div className="container my-5 py-5">
      <div id="portfolio">
        <p className="display-4 py-3">.portfolio()</p>
      </div>
      <div className="row">
        {portfoilioCardData.map((pCardData, index) => (
          <PortfolioCard key={index} pCardData={pCardData}></PortfolioCard>
        ))}
      </div>
    </div>
  );
};

export default Portfolio;
