import React from "react";
// const { convert } = require('html-to-text');

export function ToText(node) {
  let tag = document.createElement("div");
  tag.innerHTML = node;
  node = tag.innerText;
  return node;
}

const BlogCard = (props) => {
  const { title, description, link } = props.cardData;
  return (
    <a target="_blank" rel="noreferrer" href={link} className="text-decoration-none">
      <div
      className="card shadow rounded text-light m-3"
      style={{ maxWidth: "18rem", backgroundColor: "var(--light-navy)" }}
    >
      <div className="card-header bg-dark shadow">{title}</div>
      <div className="card-body">
        <p className="card-text text-secondary">{ToText(
           description.substring(0, 200)
          )}...</p>
      </div>
    </div>
    </a>
  );
};

export default BlogCard;
