import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

const PortfolioCard = (props) => {
  const { name, description, image, technology, liveLink, gitLink } = props.pCardData;
  return (
    <div
      className="card shadow rounded mb-5"
      style={{ maxWidth: "100%", backgroundColor: "var(--light-navy)" }}
    >
      <div className="row no-gutters">
        <div className="col-md-6">
          <img src={image} className="card-img shadow" alt="..." />
        </div>
        <div className="col-md-6 d-flex align-items-center">
          <div className="card-body">
            <h5 className="card-title display-4">
            {name}
            </h5>
            <p className="card-text">{description}</p>
            <div className="col-sm-6 col-md-12">
              {technology.map((item) => (
                <small className="d-inline-block text-center text-light m-2 p-2 rounded shadow bg-dark">
                  {item}
                </small>
              ))}
            </div>
            <div className="card-footer bg-transparent">
            <a href={liveLink} target="_blank" rel="noreferrer" className="mx-2">
                <FontAwesomeIcon icon={faExternalLinkAlt} />
              </a>
            <a href={gitLink} target="_blank" rel="noreferrer" className="mx-2">
                <FontAwesomeIcon icon={faGithub} />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PortfolioCard;
